/* Snazzy Infowindow */
.si-float-wrapper {
  position: absolute;
  width: 100%; }
  .si-float-wrapper,
  .si-float-wrapper * {
    box-sizing: border-box; }

[class*="si-wrapper"] {
  display: inline-flex;
  align-items: center;
  position: relative;
  max-width: 100%;
  max-height: 100%;
  max-width: 50%;
  color: #333;
  font-size: 14px; }

.si-wrapper-top {
  transform: translate(-50%, -100%);
  flex-direction: column;
  margin-top: -40px;
  margin-left: 0px; }

.si-wrapper-bottom {
  transform: translate(-50%, 0);
  flex-direction: column-reverse;
  margin-top: 0px;
  margin-left: 0px; }

.si-wrapper-left {
  transform: translate(-100%, -50%);
  margin-top: -20px;
  margin-left: -11px; }

.si-wrapper-right {
  transform: translate(0, -50%);
  flex-direction: row-reverse;
  margin-top: -20px;
  margin-left: 11px; }

.si-frame {
  flex: 1 1 auto;
  position: relative;
  z-index: 2;
  border-radius: 3px; }

.si-content-wrapper {
  width: 100%;
  height: 100%;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #eee;
  overflow: hidden; }

.si-content {
  height: 100%;
  overflow: auto;
  max-height: 200px; }

[class*="si-pointer-border"] {
  border: 15px solid transparent;
  position: absolute;
  z-index: 3; }

[class*="si-pointer-bg"] {
  border: 15px solid transparent;
  position: relative;
  z-index: 4; }

.si-pointer-border-top,
.si-pointer-border-bottom {
  left: 50%;
  transform: translate(-50%, 0); }

.si-pointer-border-left,
.si-pointer-border-right {
  top: 50%;
  transform: translate(0, -50%); }

.si-pointer-top {
  border-bottom: none; }

.si-pointer-border-top {
  bottom: 0;
  border-top-color: #eee; }

.si-pointer-bg-top {
  top: -1px;
  margin-bottom: 0px;
  border-top-color: #fff; }

.si-pointer-bottom {
  border-top: none; }

.si-pointer-border-bottom {
  top: 0;
  border-bottom-color: #eee; }

.si-pointer-bg-bottom {
  bottom: -1px;
  margin-top: 0px;
  border-bottom-color: #fff; }

.si-pointer-left {
  border-right: none; }

.si-pointer-border-left {
  right: 0;
  border-left-color: #eee; }

.si-pointer-bg-left {
  left: -1px;
  margin-right: 0px;
  border-left-color: #fff; }

.si-pointer-right {
  border-left: none; }

.si-pointer-border-right {
  left: 0;
  border-right-color: #eee; }

.si-pointer-bg-right {
  right: -1px;
  margin-left: 0px;
  border-right-color: #fff; }

[class*="si-shadow-wrapper"] {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0.25;
  z-index: 1; }

.si-shadow-wrapper-top,
.si-shadow-wrapper-bottom {
  flex-direction: column; }

.si-shadow-pointer-bottom,
.si-shadow-pointer-right {
  order: -1; }

.si-shadow-frame {
  box-shadow: 0px 3px 6px 0px #000; }

[class*="si-shadow-pointer"] {
  position: relative;
  margin: auto;
  width: 15px;
  height: 15px; }

[class*="si-shadow-inner-pointer"] {
  position: absolute;
  width: 141%;
  height: 141%;
  box-shadow: -2.12132px 2.12132px 6px 0px #000; }

.si-shadow-inner-pointer-top {
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg); }

.si-shadow-inner-pointer-bottom {
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%) rotate(-45deg); }

.si-shadow-inner-pointer-left {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg); }

.si-shadow-inner-pointer-right {
  top: 50%;
  right: 0;
  transform: translate(50%, -50%) rotate(-45deg); }
